import { ApiControllerService } from './../../services/api-controller/api-controller.service';
import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { User } from 'src/app/typings/user';
import { filter, map, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class IsAuthGuard implements CanActivate {
  constructor(private router: Router, private api: ApiControllerService) {}
  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    return this.api.user.pipe(
      map((user: User) => {
        return user.__typename === 'User';
      }),
      tap(data => {
        if (!data) {
          this.router.navigate(['/']);
        }
      })
    );
  }
}
