import { GameControllerService } from './../../services/game-controller/game-controller.service';
import { Component, OnInit } from '@angular/core';
import { faPlay, faChartBar, IconDefinition, faPlug } from '@fortawesome/free-solid-svg-icons';
import { Router } from '@angular/router';
import { ApiControllerService } from 'src/app/services/api-controller/api-controller.service';
import { Game } from 'src/app/typings/game';

@Component({
  selector: 'app-lottery-start-menu-page',
  templateUrl: './lottery-start-menu-page.component.html',
  styleUrls: ['./lottery-start-menu-page.component.scss']
})
export class LotteryStartMenuPageComponent implements OnInit {
  public icons: IconDefinition[] = [faPlay, faChartBar, faPlug];
  public showJoinModel = false;
  constructor(private gameCtrl: GameControllerService, private api: ApiControllerService, private router: Router) {}

  ngOnInit() {}

  // saveGame(game: string) {
  //   this.apollo.getClient().writeData({ data: { game } });
  // }

  onModalClose() {
    this.showJoinModel = false;
  }

  onJoin() {
    this.showJoinModel = true;
  }

  onPlay() {
    this.api.createGame().subscribe({
      next: (game: Game) => {
        this.api.saveGame(game);
        this.gameCtrl.id = game._id;
        this.router.navigate(['/dashboard/homework/loteria/game']);
      },
      error: e => {
        console.log(e);
      }
    });
  }
}
