import { LotterySheet } from 'src/app/typings/lottery';
import { Observable } from 'rxjs';
import { ApiControllerService } from './../../services/api-controller/api-controller.service';
import { Component, OnInit, Output, EventEmitter, ViewChild } from '@angular/core';
import {
  faChevronCircleRight,
  faChevronCircleLeft,
  faFolderOpen,
  IconDefinition
} from '@fortawesome/free-solid-svg-icons';
@Component({
  selector: 'app-lottery-sheets-modal',
  templateUrl: './lottery-sheets-modal.component.html',
  styleUrls: ['./lottery-sheets-modal.component.scss']
})
export class LotterySheetsModalComponent implements OnInit {
  public icons: IconDefinition[] = [faFolderOpen];
  public index = 0;
  public lotterySheets$: Observable<LotterySheet[]>;
  @Output()
  overlayClicked: EventEmitter<void> = new EventEmitter();

  constructor(private api: ApiControllerService) {
    this.lotterySheets$ = this.api.lotterySheets;
  }

  ngOnInit() {}

  onOverlayClicked(e: MouseEvent) {
    this.overlayClicked.emit();
  }
  onModalClicked(e: MouseEvent) {
    e.stopPropagation();
  }
}
