import { ApiControllerService } from 'src/app/services/api-controller/api-controller.service';
import { GameControllerService } from './../../services/game-controller/game-controller.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CanComponentDeactive } from 'src/app/guards/canLeave/can-leave.guard';
import { map, filter, tap } from 'rxjs/operators';
import { Subscription } from 'rxjs';
// import { filter } from 'graphql-anywhere';

@Component({
  selector: 'app-lottery-game-page',
  templateUrl: './lottery-game-page.component.html',
  styleUrls: ['./lottery-game-page.component.scss']
})
export class LotteryGamePageComponent implements OnInit, CanComponentDeactive {
  isStarted = false;
  isDestroyed = false;
  forceBack = false;
  private subscriptions: Subscription[] = [];

  constructor(private router: Router, private api: ApiControllerService, private gameCtrl: GameControllerService) {}
  private unsuscribe() {
    this.subscriptions.forEach(s => s.unsubscribe());
  }
  private canLeaveProcess() {
    this.unsuscribe();
    if (this.gameCtrl.isAdmin) {
      const canRemove: boolean = confirm('Are you sure you want to leave this page?');
      return this.api.removeGame(this.gameCtrl.id).pipe(
        map((data: any) => canRemove),
        filter(data => {
          if (data) {
            this.gameCtrl.reset();
          }
          console.log(data);

          return data;
        })
        // tap(() => this.gameCtrl.reset())
      );
    } else if (!this.gameCtrl.isAdmin && !this.isDestroyed) {
      const canRemove: boolean = confirm('Are you sure you want to leave this page?');

      return this.api.leaveGame(this.gameCtrl.id).pipe(
        map(() => {
          if (canRemove) {
            this.gameCtrl.reset();
          }
          return canRemove;
        })
      );
    } else if (!this.gameCtrl.isAdmin && this.isDestroyed) {
      return true;
    } else {
      return false;
    }
  }

  public canDeactive() {
    if (this.forceBack) {
      return true;
    } else {
      return this.canLeaveProcess();
    }
  }

  onClose() {
    this.forceBack = true;
    this.router.navigate(['/dashboard/homework/loteria/menu']);
    this.gameCtrl.reset();
  }

  ngOnInit() {
    this.OnWin();
    this.onStartGame();
    this.onDestroyGame();
  }
  // Private
  private onDestroyGame(): void {
    const s = this.api
      .subscribeOnDestroyGame(this.gameCtrl.id)
      .pipe(filter(() => !this.gameCtrl.isAdmin))
      .subscribe(data => {
        // this.gameCtrl.reset();
        this.isDestroyed = true;
        this.router.navigate(['/dashboard/homework/loteria/menu']);
      });
    this.subscriptions.push(s);
  }
  private onStartGame(): void {
    const s = this.api.subscribeStartGame(this.gameCtrl.id).subscribe(data => {
      // console.log(data);
      this.isStarted = true;
      this.gameCtrl.isStarted = true;
    });
    this.subscriptions.push(s);
  }
  private OnWin(): void {
    const s = this.api.subscribeOnWin(this.gameCtrl.id).subscribe((winner: any) => {
      console.log('CHANGE UI TO WIN UI');
      console.log(winner);

      this.gameCtrl.addWinner(winner);
    });
    this.subscriptions.push(s);
  }

  // GET
  get isWon() {
    return this.gameCtrl.isWon;
  }
}
