import { GameControllerService } from './../../services/game-controller/game-controller.service';
import { Component, OnInit } from '@angular/core';
import { ApiControllerService } from 'src/app/services/api-controller/api-controller.service';
import { Player } from 'src/app/typings/player';
import { User } from 'src/app/typings/user';
import { Observable, zip } from 'rxjs';
import { map, take } from 'rxjs/operators';

@Component({
  selector: 'app-online-user-list',
  templateUrl: './online-user-list.component.html',
  styleUrls: ['./online-user-list.component.scss']
})
export class OnlineUserListComponent implements OnInit {
  // players: Player[] = [];
  // user: User;
  gameAdminID$: Observable<string>;

  constructor(private gameCtrl: GameControllerService, private api: ApiControllerService) {
    this.gameAdminID$ = this.api.gameAdminID;
  }

  private getGameAdmin$() {
    return this.api.gameAdmin.pipe(
      map((admin: User) => {
        return { _id: admin._id, name: admin.fullname, isReady: true, avatar: admin.avatar };
      })
    );
  }

  private getUser$() {
    return this.api.user.pipe(
      map((user: User) => {
        return { _id: user._id, name: user.fullname, isReady: false, avatar: user.avatar };
      })
    );
  }

  private getGamePlayers$(id: string) {
    return this.api.gamePlayers(id);
  }

  ngOnInit() {
    const source = zip(this.getGameAdmin$(), this.getUser$(), this.getGamePlayers$(this.gameCtrl.id));
    source.pipe(take(1)).subscribe(([admin, user, players]: any) => {
      if (admin._id !== user._id) {
        this.gameCtrl.addPlayer(admin);
      }
      this.gameCtrl.player = user;
      this.gameCtrl.addPlayer({ ...user, isReady: admin._id === user._id });
      // console.log(players.map());
      (players as Player[]).map(player => {
        this.gameCtrl.addPlayer({ ...player, isReady: false });
      });
    });

    this.joinGame(this.gameCtrl.id);
    this.ready(this.gameCtrl.id);
    this.leaveGame(this.gameCtrl.id);
  }

  private leaveGame(id: string) {
    this.api.subscribeLeaveGame(id).subscribe(playerID => {
      console.log(`Removed ${playerID}`);
      this.gameCtrl.removePlayer(playerID);
    });
  }
  private joinGame(id: string): void {
    this.api.subscribeJoinGame(id).subscribe(player => {
      this.gameCtrl.addPlayer(player);
    });
  }
  private ready(id: string): void {
    this.api.subscribeReadyGame(id).subscribe((id: string) => {
      console.log(`USER ${id} is ready.`);

      this.gameCtrl.setPlayerIsReady(id);
    });
  }

  get players() {
    return this.gameCtrl.players;
  }
}
