import { BlockNumberComponent } from './../../../../components/block-number/block-number.component';
import {
  Component,
  OnInit,
  ViewChild,
  ElementRef,
  Renderer2,
  AfterViewInit,
  ViewChildren,
  QueryList
} from '@angular/core';
import { faTrash } from '@fortawesome/free-solid-svg-icons';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { map, filter, debounceTime, startWith, tap } from 'rxjs/operators';
import { Observable } from 'rxjs';
@Component({
  selector: 'app-sieve-of-eratosthenes',
  templateUrl: './sieve-of-eratosthenes.component.html',
  styleUrls: ['./sieve-of-eratosthenes.component.scss']
})
export class SieveOfEratosthenesComponent implements OnInit, AfterViewInit {
  public removeIcon = faTrash;
  public sieveForm: FormGroup;
  public total$: Observable<number[]>;
  @ViewChild('sieveGrid')
  private grid: ElementRef;
  @ViewChildren(BlockNumberComponent)
  private blocks: QueryList<BlockNumberComponent>;

  constructor(private renderer: Renderer2) {
    this.sieveForm = new FormGroup({
      total: new FormControl('20', [Validators.required, Validators.min(20)])
    });
  }

  ngAfterViewInit(): void {
    // if (this.grid !== null) console.log(this.grid.style);
    // this.grid.style
    // console.log(this.grid.style);
    // this.renderer.setStyle(this.grid, ' grid-template-columns', 'repeat(5,1fr)');
  }

  ngOnInit() {
    this.total$ = this.sieveForm.get('total').valueChanges.pipe(
      tap(() => {
        if (this.blocks) {
          this.clear();
        }
      }),
      debounceTime(500),
      startWith(20),
      filter((value: number) => value >= 20),
      tap(value => {
        if (value >= 20 && value < 60) {
          this.grid.nativeElement.style.gridTemplateColumns = 'repeat(5,1fr)';
        } else if (value >= 60 && value < 70) {
          this.grid.nativeElement.style.gridTemplateColumns = 'repeat(6,1fr)';
        } else if (value >= 70 && value <= 90) {
          this.grid.nativeElement.style.gridTemplateColumns = 'repeat(8,1fr)';
        } else {
          this.grid.nativeElement.style.gridTemplateColumns = 'repeat(10,1fr)';
        }
        // this.clear();
      }),
      map(value => Array.from(Array(value).keys()))
    );
  }

  multiplyOf(value: number) {
    // console.log('All multiplies of ' + value);
    this.blocks.forEach(block => {
      if (block.getValue() % value === 0) {
        block.setBackground(`mul-${value}`);
        block.setIsSelected(true);
      }
    });
    // console.log(this.blocks.first);
  }
  clear() {
    this.blocks.forEach(block => block.reset());
  }
  onPrime() {
    const aux = [];

    this.blocks.forEach(b => {
      const value = b.getValue();
      if (value === 1 || aux.indexOf(value) !== -1) {
        return;
      }
      for (let i = value + 1; i <= this.blocks.length; i++) {
        if (i % value === 0) {
          aux.push(i);
        }
      }
      console.log(`${value} is prime`);
      b.isPrime();
    });

    console.log(aux);

    // e.style.border = '5px dotted #FF9800';
    // e.classList += ' prime';
    // e.style.backgroundColor = '#212121';
  }
}
