import { LotteryGamePageComponent } from './../lottery-game-page/lottery-game-page.component';
import { LoteriaComponent } from './../homeworks-page/homeworks/loteria/loteria.component';
import { CurriculumComponent } from './../homeworks-page/homeworks/curriculum/curriculum.component';
import { HomeworkViewPageComponent } from './../homework-view-page/homework-view-page.component';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DashboardPageComponent } from './dashboard-page.component';
import { Routes, RouterModule } from '@angular/router';
import { AsideMenuModule } from 'src/app/components/aside-menu/aside-menu.module';
import { StyleMatrixComponent } from '../homeworks-page/homeworks/style-matrix/style-matrix.component';
import { HomeworkViewPageModule } from '../homework-view-page/homework-view-page.module';
import { ScheduleComponent } from '../homeworks-page/homeworks/schedule/schedule.component';
import { SieveOfEratosthenesComponent } from '../homeworks-page/homeworks/sieve-of-eratosthenes/sieve-of-eratosthenes.component';
import { LotteryStartMenuPageComponent } from '../lottery-start-menu-page/lottery-start-menu-page.component';
import { LotteryScorePageComponent } from '../lottery-score-page/lottery-score-page.component';
import { IsAuthGuard } from 'src/app/guards/isAuth/is-auth.guard';
import { CanLeaveGuard } from 'src/app/guards/canLeave/can-leave.guard';

const homeworksRoutes: Routes = [
  {
    path: 'style-matrix',
    component: StyleMatrixComponent,
    canActivate: [IsAuthGuard]
  },
  {
    path: 'curriculum',
    component: CurriculumComponent,
    canActivate: [IsAuthGuard]
  },
  {
    path: 'schedule',
    component: ScheduleComponent,
    canActivate: [IsAuthGuard]
  },
  {
    path: 'sieve-of-eratosthenes',
    component: SieveOfEratosthenesComponent,
    canActivate: [IsAuthGuard]
  },
  {
    path: 'loteria',
    component: LoteriaComponent,
    canActivate: [IsAuthGuard],

    children: [
      {
        path: '',
        redirectTo: 'menu'
      },
      {
        path: 'menu',
        component: LotteryStartMenuPageComponent
      },
      {
        path: 'game',
        component: LotteryGamePageComponent,
        canDeactivate: [CanLeaveGuard]
      },
      {
        path: 'score',
        component: LotteryScorePageComponent
      }
    ]
  }
];

const routes: Routes = [
  {
    path: '',
    component: DashboardPageComponent,
    children: [
      {
        path: '',
        redirectTo: 'home',
        pathMatch: 'full'
      },
      {
        path: 'home',
        loadChildren: '../home-page/home-page.module#HomePageModule'
      },
      {
        path: 'homeworks',
        loadChildren: '../homeworks-page/homeworks-page.module#HomeworksPageModule',
        canActivate: [IsAuthGuard]
      },
      {
        path: 'homework',
        component: HomeworkViewPageComponent,
        children: homeworksRoutes
      },
      {
        path: 'activities',
        loadChildren: '../activities-page/activities-page.module#ActivitiesPageModule',
        canActivate: [IsAuthGuard]
      }
    ]
  }
];

@NgModule({
  declarations: [DashboardPageComponent],
  exports: [DashboardPageComponent],
  imports: [CommonModule, RouterModule.forChild(routes), AsideMenuModule, HomeworkViewPageModule]
})
export class DashboardPageModule {}
