import { GameControllerService } from './../../services/game-controller/game-controller.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import {
  faStar,
  faPlay,
  faHandHoldingUsd,
  faRandom,
  IconDefinition,
  faTrash,
  faSave,
  faFileDownload,
  faDoorOpen
} from '@fortawesome/free-solid-svg-icons';
import { User } from 'src/app/typings/user';
import { ApiControllerService } from 'src/app/services/api-controller/api-controller.service';
import { Game } from 'src/app/typings/game';
import { zip } from 'rxjs';
import { Router } from '@angular/router';
import { take } from 'rxjs/operators';

@Component({
  selector: 'app-lottery-board-menu',
  templateUrl: './lottery-board-menu.component.html',
  styleUrls: ['./lottery-board-menu.component.scss']
})
export class LotteryBoardMenuComponent implements OnInit {
  public icons: IconDefinition[] = [
    faStar,
    faRandom,
    faHandHoldingUsd,
    faPlay,
    faSave,
    faTrash,
    faFileDownload,
    faDoorOpen
  ];
  public priceForm: FormGroup;
  public isSetPrice = false;
  private user: User;
  private game: Game;

  @Output()
  OnEventRandom: EventEmitter<void> = new EventEmitter();
  @Output()
  $selectSheet: EventEmitter<void> = new EventEmitter();

  constructor(private router: Router, private gameCtrl: GameControllerService, private api: ApiControllerService) {
    this.priceForm = new FormGroup({
      price: new FormControl(1, [Validators.required, Validators.min(1), Validators.max(5)])
    });
  }

  ngOnInit() {
    zip(this.api.game, this.api.user).subscribe(([game, user]: any) => {
      this.game = game;
      this.user = user;
      this.gameCtrl.isAdmin = this.game.admin._id === this.user._id;
    });
    // this.isLoading=true;
  }

  // private

  // Listeners
  onLeave() {
    this.gameCtrl.isDone = true;
    // this.gameCtrl.removePlayer(this)
    this.router.navigate(['/dashboard/homework/loteria/menu']);
  }
  onSelectSheet() {
    console.log('OPEN MODAL');
    this.$selectSheet.emit();
  }

  onRandom() {
    console.log('ON RANDOM');
    this.OnEventRandom.emit();
  }

  onReady() {
    console.log('ON READY');
    console.log(this.gameCtrl.player);
    console.log(this.gameCtrl.lotterySheet);

    this.gameCtrl.player.isReady = true;
    this.api.ready(this.game._id).subscribe({
      next: data => {
        console.log(data);
      },
      error: e => {
        console.log(e);
      }
    });
  }

  onPlay() {
    this.api.startGame(this.game._id).subscribe(data => {
      console.log(data);
      this.gameCtrl.startGame();
      this.isSetPrice = false;
    });
  }
  onPrice() {
    console.log('ON PRICE');
    this.isSetPrice = true;
  }

  onSetPrice(isAdd: boolean) {
    const currentValue = this.priceField.value;
    let price = currentValue;
    if (isAdd && this.price < 5) {
      price++;
    } else if (!isAdd && this.price > 1) {
      price--;
    }
    this.api
      .updateLotterySheetPrice(this.game._id, price)
      .subscribe((sheetPrice: number) => (this.game.sheetPrice = sheetPrice));
    this.priceField.setValue(price);
    this.gameCtrl.setPrice(price);
  }

  private get priceField() {
    return this.priceForm.get('price');
  }
  get price() {
    return this.priceField.value;
  }

  get isReady() {
    return this.gameCtrl.isReady;
  }

  get isStarted() {
    return this.gameCtrl.isStarted;
  }

  get playerIsReady() {
    return this.gameCtrl.player ? this.gameCtrl.player.isReady : false;
  }

  get isAdmin() {
    return this.gameCtrl.isAdmin;
  }

  get hasCardPushed() {
    return this.gameCtrl.hasCardsPushed;
  }
}
