import { GameControllerService } from './../../services/game-controller/game-controller.service';
import { Observable } from 'rxjs';
import { ApiControllerService } from 'src/app/services/api-controller/api-controller.service';
import { LotterySheet } from 'src/app/typings/lottery';
import { Component, OnInit, Input, ElementRef, ViewChild } from '@angular/core';
import {
  faChevronCircleRight,
  faChevronCircleLeft,
  faTrash,
  faCheckCircle,
  IconDefinition
} from '@fortawesome/free-solid-svg-icons';
import { tap } from 'rxjs/operators';
@Component({
  selector: 'app-lottery-sheets-slideshow',
  templateUrl: './lottery-sheets-slideshow.component.html',
  styleUrls: ['./lottery-sheets-slideshow.component.scss']
})
export class LotterySheetsSlideshowComponent implements OnInit {
  public icons: IconDefinition[] = [faChevronCircleLeft, faChevronCircleRight, faCheckCircle, faTrash];
  public isLoading = true;
  public index = 0;
  @Input()
  public lotterySheets: LotterySheet[];
  @ViewChild('slide')
  slide: ElementRef;

  constructor(private gameCtrl: GameControllerService, private api: ApiControllerService) {}

  ngOnInit() {
    // this.lotterySheets$ = this.api.lotterySheets;
    const aux = this.api.lotterySheets.pipe(tap(() => (this.isLoading = true))).subscribe({
      next: lotterySheets => {
        this.lotterySheets = lotterySheets;
        this.isLoading = false;
      }
    });
    // aux.unsubscribe();
  }

  onSelect() {
    console.log('SELECT');
    console.log();
    this.gameCtrl.lotterySheet = this.lotterySheets[this.index].sheet;
    this.gameCtrl.isRandomSheet = false;
  }
  onRemove() {
    const id: string = this.lotterySheets[this.index]._id;
    // console.log(`Remove -> ${}`);
    this.api.deleteLotterySheet(id).subscribe(data => console.log(`${data} was deleted`));
  }

  prev() {
    console.log(`Index: ${this.index}`);

    if (!this.index) {
      return;
      // this.index = this.lotterySheets.length - 1;
      // this.slide.nativeElement.style = `left:${(this.lotterySheets.length - 1) * -100}%`;
    } else {
      this.index--;
      this.slide.nativeElement.style = `left:${this.index * -100}%`;
    }
  }
  next() {
    console.log(`Index: ${this.index}\n Lottery sheets: ${this.lotterySheets.length}`);
    if (this.index === this.lotterySheets.length - 1) {
      // this.index = 0;
      // this.slide.nativeElement.style = `left:0%`;
      return;
    } else {
      this.index++;
      this.slide.nativeElement.style = `left:${this.index * -100}%`;
    }
  }
}
