import { GameControllerService } from './../../services/game-controller/game-controller.service';
import { Observable } from 'rxjs';
import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { faTimesCircle, faGamepad, faMale, IconDefinition } from '@fortawesome/free-solid-svg-icons';
import { ApiControllerService } from 'src/app/services/api-controller/api-controller.service';
import { Game } from 'src/app/typings/game';
import { Router } from '@angular/router';
@Component({
  selector: 'app-game-list-modal',
  templateUrl: './game-list-modal.component.html',
  styleUrls: ['./game-list-modal.component.scss']
})
export class GameListModalComponent implements OnInit {
  public icons: IconDefinition[] = [faTimesCircle, faGamepad, faMale];
  public games$: Observable<Game[]>;
  @Output()
  eventClose: EventEmitter<void> = new EventEmitter();
  constructor(private gameCtrl: GameControllerService, private api: ApiControllerService, private router: Router) {}

  ngOnInit() {
    console.log('ONOT');
    this.games$ = this.api.unStartedGames;
  }
  onClick(id: string) {
    console.log(`Click on game ${id} `);
    this.api.joinGame(id).subscribe({
      next: (game: any) => {
        this.gameCtrl.id = id;
        this.api.saveGame(game);
        this.router.navigate(['/dashboard/homework/loteria/game']);
      },
      error: e => {
        console.error(e);
      }
    });
  }

  // get
}
