export class LotterySheetUtils {
  private get lotterySheetImages(): string[] {
    return Array.from(Array(54).keys())
      .map(v => v + 1)
      .map(v => (v < 10 ? `0${v}` : `${v}`))
      .map(v => `${v}.jpg`);
  }

  // Generate images for the lottery sheet
  public generateLotterySheetImages(n = 16): string[] {
    const lotteryImages = this.shuffle(this.lotterySheetImages);
    return this.generateLotterySheetIndexes(n).map(i => lotteryImages[i]);
  }
  // Generate image indexes without duplicates.
  private generateLotterySheetIndexes(n: number) {
    const res: number[] = [];
    while (res.length < n) {
      const n: number = Math.floor(Math.random() * 54);
      if (!res.includes(n)) {
        res.push(n);
      }
    }
    return res;
  }
  // it shuffles an array
  private shuffle(array: string[]): string[] {
    return array.sort(() => Math.random() - 0.5);
  }
}
