import { GameControllerService } from './../../services/game-controller/game-controller.service';
import { LotterySheetUtils } from './../../utils/lottery-sheet-utils/lottery-sheet-utils';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-lottery-sheet',
  templateUrl: './lottery-sheet.component.html',
  styleUrls: ['./lottery-sheet.component.scss']
})
export class LotterySheetComponent implements OnInit {
  // public lotterySheet: string[];
  public isLoading = false;
  private lotteryUtils: LotterySheetUtils;

  constructor(private gameCtrl: GameControllerService) {
    this.lotteryUtils = new LotterySheetUtils();
  }

  ngOnInit() {
    this.random();
  }

  public random() {
    this.gameCtrl.lotterySheet = this.lotteryUtils.generateLotterySheetImages();
    this.gameCtrl.isRandomSheet = true;
  }

  get lotterySheet() {
    return this.gameCtrl.lotterySheet;
  }
  ///
}
