import { GameControllerService } from './../../services/game-controller/game-controller.service';
import { Component, OnInit, Input } from '@angular/core';
import { ApiControllerService } from 'src/app/services/api-controller/api-controller.service';

@Component({
  selector: 'app-lottery-card',
  templateUrl: './lottery-card.component.html',
  styleUrls: ['./lottery-card.component.scss']
})
export class LotteryCardComponent {
  private row: number;
  private column: number;
  public isSelected = false;
  @Input()
  index: number;
  @Input()
  img: string;

  constructor(private api: ApiControllerService, private gameCtrl: GameControllerService) {}

  private getRowColumn(index: number) {
    if (index < 4) {
      return [0, index];
    } else if (index > 3 && index < 8) {
      return [1, index - 4];
    } else if (index > 7 && index < 12) {
      return [2, index - 8];
    } else {
      return [3, index - 12];
    }
  }
  onClick() {
    const [row, col] = this.getRowColumn(this.index);
    this.column = col;
    this.row = row;
    if (!this.gameCtrl.isStarted) {
      alert('Hey! first all the players must be ready.');
      return;
    }
    if (!this.isSelected && this.isClickeable) {
      this.isSelected = true;
      // console.log(`Click on row: ${this.row} and column: ${this.column} --> Index: ${this.index}`);

      this.gameCtrl.selectCard(this.row, this.column);

      if (this.gameCtrl.checkWin()) {
        this.api.win(this.gameCtrl.id, this.img).subscribe();
        console.log('GANASTE!');
        this.gameCtrl.isWon = true;
      }

      if (this.gameCtrl.hasWinners) {
        this.gameCtrl.isWon = true;
      }
    } else {
      console.log('No mames ya le aplanaste');
    }
    // if()
  }

  get isClickeable() {
    return this.gameCtrl.cardHistorial.includes(this.img);
  }
}
