import { ApiControllerService } from 'src/app/services/api-controller/api-controller.service';
import { Component, OnInit, Input } from '@angular/core';
import { faCircle, faStar, faChild, IconDefinition } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'app-online-user',
  templateUrl: './online-user.component.html',
  styleUrls: ['./online-user.component.scss']
})
export class OnlineUserComponent implements OnInit {
  public circleOn: IconDefinition = faCircle;
  public adminIcon: IconDefinition = faStar;
  public userIcon: IconDefinition = faChild;
  public user$;

  @Input()
  id: string;
  @Input()
  avatar: string;
  @Input()
  name: string;
  @Input()
  isAdmin: boolean;
  @Input()
  isReady: boolean;

  constructor(private api: ApiControllerService) {
    this.user$ = this.api.userID;
  }

  ngOnInit() {}
}
