import { GameControllerService } from './../../services/game-controller/game-controller.service';
import { Component, OnInit } from '@angular/core';
import { faHome, faLaptopCode, faShapes, faSignOutAlt } from '@fortawesome/free-solid-svg-icons';
import { faGithubAlt } from '@fortawesome/free-brands-svg-icons';
import { GET_USER } from 'src/app/graphQL/queries/query/local/get-user';
import { Apollo } from 'apollo-angular';
import { User } from 'src/app/typings/user';
import { Router } from '@angular/router';
import { tap, map, catchError } from 'rxjs/operators';
import { Observable, throwError } from 'rxjs';
@Component({
  selector: 'app-aside-menu',
  templateUrl: './aside-menu.component.html',
  styleUrls: ['./aside-menu.component.scss']
})
export class AsideMenuComponent implements OnInit {
  public user$: Observable<User>;
  public isGuest = true;
  public isLoading = false;

  public homeIcon = faHome;
  public homeworkIcon = faLaptopCode;
  public inClassActivitiesIcon = faShapes;
  public githubIcon = faGithubAlt;
  public logoutIcon = faSignOutAlt;

  constructor(private apollo: Apollo, private router: Router, private gameCtrl: GameControllerService) {
    this.user$ = this.apollo.query({ query: GET_USER }).pipe(
      catchError((e: any) => {
        console.log('ERRORR!');

        return throwError(e);
      }),
      map((data: any): User => data.data.user),
      tap(user => {
        // console.log(user.__typename === 'GUEST');
        // console.log(user);

        this.isGuest = user.__typename === 'Guest';
      })
    );
  }

  ngOnInit() {}

  logout() {
    localStorage.removeItem('token');
    this.router.navigate(['/i/signin']);
    this.gameCtrl.reset();
    this.apollo.getClient().resetStore();
  }

  onGithub() {
    window.open('https://github.com/NachoCodeX');
  }

  get isPlaying() {
    return this.gameCtrl.player;
  }
}
