import { Component, OnInit, Input, Renderer2, ViewChild, ElementRef } from '@angular/core';

@Component({
  selector: 'app-block-number',
  templateUrl: './block-number.component.html',
  styleUrls: ['./block-number.component.scss']
})
export class BlockNumberComponent implements OnInit {
  @Input()
  private value: number;
  private isSelected = false;
  private color: string;
  @ViewChild('block')
  element: ElementRef;

  constructor(private renderer: Renderer2) {}

  ngOnInit() {}

  getValue() {
    return this.value;
  }
  setIsSelected(isSelected: boolean) {
    this.isSelected = isSelected;
  }
  setBackground(color: string) {
    if (!this.isSelected) {
      this.color = color;
      this.renderer.addClass(this.element.nativeElement, color);
    }
  }
  isPrime() {
    this.renderer.addClass(this.element.nativeElement, 'prime');
  }
  reset() {
    this.isSelected = false;
    this.renderer.removeClass(this.element.nativeElement, this.color);
    this.renderer.removeClass(this.element.nativeElement, 'prime');
  }

  // setValue()
}
