import { ApiControllerService } from './../../services/api-controller/api-controller.service';
import { GameControllerService } from './../../services/game-controller/game-controller.service';
import { LotterySheetUtils } from './../../utils/lottery-sheet-utils/lottery-sheet-utils';
import { Component, OnInit } from '@angular/core';
import { faStickyNote, IconDefinition } from '@fortawesome/free-solid-svg-icons';
import { filter } from 'rxjs/operators';

@Component({
  selector: 'app-lottery-cards-view',
  templateUrl: './lottery-cards-view.component.html',
  styleUrls: ['./lottery-cards-view.component.scss']
})
export class LotteryCardsViewComponent implements OnInit {
  private lotterySheetUtils: LotterySheetUtils;
  private _cards: string[] = [];
  public cards: string[] = [];
  public cardIcon: IconDefinition = faStickyNote;
  // private showCard$: Subject<void> = new Subject();

  constructor(private api: ApiControllerService, private gameCtrl: GameControllerService) {
    this.lotterySheetUtils = new LotterySheetUtils();
    this._cards = this.lotterySheetUtils.generateLotterySheetImages(54);
  }

  ngOnInit() {
    this.api
      .subscribeOnNewCard(this.gameCtrl.id)
      .pipe(filter(data => !this.gameCtrl.isAdmin))
      .subscribe(data => {
        console.log('NEW CARD');
        console.log(data);
        if (this.cards.length === 3) {
          this.cards.pop();
        }
        this.cards.unshift(data);
        this.gameCtrl.cardHistorial.push(data);
      });
  }

  private _pushCard() {
    if (this.cards.length === 3) {
      this.cards.pop();
    }
    const card = this._cards.shift();
    this.cards.unshift(card);
    this.gameCtrl.cardHistorial.push(card);
  }

  pushCard() {
    this._pushCard();
    this.api.newCard(this.gameCtrl.id, this.cards[0]).subscribe((data: any) => console.log(data));
  }

  finishGame() {
    this.gameCtrl.isWon = true;
  }

  // GET
  get isReady() {
    return this.gameCtrl.isReady;
  }

  get isAdmin() {
    return this.gameCtrl.isAdmin;
  }

  get hasWinners() {
    return this.gameCtrl.hasWinners;
  }
}
