import { ApiControllerService } from 'src/app/services/api-controller/api-controller.service';
import { Component, OnInit } from '@angular/core';
import { faTrash, faSave, faTrophy, faFileDownload, IconDefinition } from '@fortawesome/free-solid-svg-icons';
import { GameControllerService } from 'src/app/services/game-controller/game-controller.service';
import { LotterySheet } from 'src/app/typings/lottery';

@Component({
  selector: 'app-win-view-menu',
  templateUrl: './win-view-menu.component.html',
  styleUrls: ['./win-view-menu.component.scss']
})
export class WinViewMenuComponent implements OnInit {
  public icons: IconDefinition[] = [faTrophy, faSave, faTrash, faFileDownload];
  public isSaved = false;
  constructor(private gameCtrl: GameControllerService, private api: ApiControllerService) {}

  ngOnInit() {}

  // Listeners
  onSave() {
    this.api.saveLotterySheet(this.gameCtrl.lotterySheet).subscribe({
      next: (lottery: LotterySheet) => {
        console.log(lottery);
        this.isSaved = true;
      },
      error: e => {
        console.log(e);
      }
    });
  }
  onRemove() {
    console.log('REMOVED');
    // this.isRemoved = true;
    this.isSaved = false;
    // this.is
  }

  get hasCardPushed() {
    return this.gameCtrl.hasCardsPushed;
  }

  get isRandomeSheet() {
    return this.gameCtrl.isRandomSheet;
  }
}
